       url('./fonts/onlinefontconverter.com/converted-files/CoolveticaCondensedRg-Regular.ttf') format('truetype'),
       url('./fonts/onlinefontconverter.com/converted-files/CoolveticaCondensedRg-Regular.woff') format('woff');
