/*dddddddddddddddd*/
.btn{
  background-color: #cddc39;
  color: #9e9d24;
  outline: 0;
  padding: 5px 10px;
  font-size: 2vh;
  border: 0.2px solid #c0ca33;
  border-radius: 5px;
  cursor: pointer;
  
  box-shadow: 0px 2px 5px #c6ff00;
  text-transform: uppercase;
  transition: ease background-color 0.25s;
  transition: ease box-shadow 0.25s;
}
.btn:hover{
  background-color: #f0f4c3;
  box-shadow: 0.5px 0.5px 2px #c6ff00;
}
